.booknowmain {
    padding: 5px;
    box-shadow: 1px 1px 15px rgb(105, 105, 107);
    justify-content: center;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    background-image: linear-gradient(#ca97fc, #7da4ff);
}

.formdivp {
    margin-top: 20px;
    margin-bottom: 20px;
    margin-left: 10px;
    margin-right: 10px;
    background: #ffffff;
    border-radius: 10px;
    box-shadow: 1px 1px 15px rgb(105, 105, 107);
    max-width: 400px;
    width: 100%;
    height: auto;
    align-items: center;
    text-align: center;
}

.formdiv {
    max-width: 400px;
    width: 100%;
    align-items: center;
    text-align: center;
    height: fit-content;
    
}

input {
    font-size: 18px;
    display: inline-block;
    border: 1px solid rgb(92, 103, 139);
    border-radius: 4px;
    box-sizing: border-box;
    padding: 12px 20px;
    margin: 2px;
    max-width: 280px;
    width: 100%;
    margin-top: 5px;
    margin-bottom: 5px;
}

select {
    font-size: 18px;
    display: inline-block;
    border: 1px solid rgb(92, 103, 139);
    border-radius: 4px;
    background: #ffffff;
    box-sizing: border-box;
    padding: 12px 20px;
    margin: 2px;
    max-width: 280px;
    width: 100%;
    margin-top: 5px;
    margin-bottom: 5px;
}

input[type=reset] {
    background-color: #ffffff;
    border: 1px solid #3f51b5;
    color: black;
    padding: 10px 32px;
    text-decoration: none;
    margin: 4px 2px;
    cursor: pointer;
    width: 130px;
}

input[type=submit] {
    background-color: #3f51b5;
    color: white;
    padding: 10px 32px;
    text-decoration: none;
    margin: 4px 2px;
    cursor: pointer;
    width: 130px;
}

input[type=submit]:hover {
    background-color: #a0d2fa;
    border: 1px solid #3f51b5;
    color: black;
    padding: 10px 32px;
    text-decoration: none;
    margin: 4px 2px;
    cursor: pointer;
    width: 130px;
}

input[type=reset]:hover {
    background-color: #3f51b5;
    color: white;
    padding: 10px 32px;
    text-decoration: none;
    margin: 4px 2px;
    cursor: pointer;
    width: 130px;
}
#table{
    width: 100%;
    padding-left: 10px;
    padding-right: 10px;
}
