.IntroDivMain{
padding: 10px;
margin: 10px;

box-shadow: 1px 1px 15px rgb(105, 105, 107);
border-radius: 15px;
background: #ffffff;
}

@media only screen and (max-width: 913px) {
    #imgcar {
     width: 330px;
     height: 180px;
  }
  #pichori{
    width: 100%;
    height: 200px;
  }
}

@media only screen and (min-width: 913px) {

#pichori{
    width: 100%;
    height: 350px;
}
}

.IntroDivMain2{
    padding: 5px;
    margin-top: 10px;
    margin-bottom: 10px;
    box-shadow: 1px 1px 15px rgb(105, 105, 107);
    justify-content: center;
    background: #ffffff;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    
}
.itemdivs{
    padding: 5px;
    margin: 5px;
    box-shadow: 1px 0px 15px rgb(105, 105, 107);
    border-radius: 5px;
    width: 330px;
    display: flex;
    background: #ffffff;
    flex-direction: row;
}
#picitem{
    width: 128px;
    height: 128px;
}
.IntrodivText{
    display: flex;
    flex-direction: column;
}

.headingline{
    font-size: 20px;
color: black;
font-weight: 500;

}
.pricing{
    font-size: 18px;
    color: rgb(51, 46, 46);
    font-weight: 400;
    margin-top: 5px;
}
.booknowbt{
    color: #ffffff;
    margin: 5px;
    padding: 5px;
    background: #4b038f;
    text-decoration: none;
    font-size: 20px;
    border-radius: 5px;
    width: 140px;
    display: flex;
    cursor: pointer;
    white-space: nowrap;
}

.booknowbtitem{
    color: #ffffff;
    margin: 5px;
    padding: 5px;
    background: #035f91;
    text-decoration: none;
    font-size: 16px;
    border-radius: 5px;
    
    display: flex;
    cursor: pointer;
    white-space: nowrap;
}
.booknowbtpreview{
    color: #ffffff;
    margin: 5px;
    padding: 5px;
    background: #a404b3;
    font-size: 16px;
    border-radius: 5px;
    border: none;
    cursor: pointer;
   
}

.datadiv{
    width: 300px;
    min-height: 400px;
    position: absolute ;
    z-index: 999;
    padding: 10px;
    background: #ffffff;
  
    left:50%;
    margin: 0 0 0 -170px;
    border-radius:5px;
    border: #ccc;
    box-shadow: 1px 0px 30px rgb(146, 146, 153);
}
.datadivinner{
    width: 300px;
    min-height: 400px;
    background: #ffffff;
    padding: 10px;
}

.Reachus{
    padding: 5px;
    margin-top: 10px;
    margin-bottom: 10px;
    box-shadow: 1px 1px 15px rgb(105, 105, 107);
    justify-content: center;
    background: #ffffff;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    
}

.Tracknowbt{
    color: #ffffff;
    margin: 5px;
